.search-list .search-item {
  display: flex;
  align-items: center;
  height: 40px;
  overflow: hidden;
}
.search-list .search-item.list-enter-active,
.search-list .search-item.list-leave-active {
  transition: all 0.1s;
}
.search-list .search-item.list-enter-from,
.search-list .search-item.list-leave-to {
  height: 0;
}
.search-list .search-item .text {
  flex: 1;
  color: rgba(255,255,255,0.5);
}
.search-list .search-item .icon {
  position: relative;
}
.search-list .search-item .icon:before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}
.search-list .search-item .icon .icon-delete {
  font-size: 12px;
  color: rgba(255,255,255,0.3);
}
